import {useState, useCallback, useRef, useEffect} from "react";
// We use useCallback as this function is neer get recreated
export const useHttpClient = () => {
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState();

    const activeHttpRequests = useRef([]);   // To Cancel the Request if it's not active

    const sendRequest = useCallback (async (url, method = 'GET', body = null, headers = {}) => {
        setIsLoading(true);
        const httpAbortController = new AbortController();   // To Cancel the Request if it's not active
        activeHttpRequests.current.push(httpAbortController);  // To Cancel the Request if it's not active
        try{
            const response = await fetch(url, {
            method, 
            body, 
            headers,
            signal: httpAbortController.signal    // Sending a signal to the controller inside the request.
        });
    
        const responseData = await response.json();

        // If a Request completes the abortcontrollers should be removed from the array.
        // This keeps all the other controllers except the controller which is used.
        activeHttpRequests.current = activeHttpRequests.current.filter(reqCtrl => reqCtrl !== httpAbortController);

        // To pass the If check if we stay in the success response we use Return.
        if (!response.ok) {
        throw new Error(responseData.message);
      }
      
    setIsLoading(false);
      return responseData;  
    }
    catch(err){
        setError(err.message);
        setIsLoading(false);
        throw err;
    }

    }, []); 

    const clearError = () => {
    setError(null); 
    }
    // To clean up the logic when a component unmounts
    useEffect(() => {
        return() => {
            activeHttpRequests.current.forEach(abortCtrl => abortCtrl.abort());
        };
    }, []);
    return{isLoading, error, sendRequest, clearError};
};